"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Alert, AlertDescription } from "@/components/ui/alert"

interface DatabaseConfig {
  host: string
  user: string
  password: string
  database: string
  port: string
}

interface DBProduct {
  id: number
  name: string
  description: string
  price: number
  category: string
  stock: number
  image_url: string
  created_at: string
  updated_at: string
}

export default function DatabasePage() {
  const [connectionString, setConnectionString] = useState<string>(
    typeof window !== "undefined" ? localStorage.getItem("db_connection_string") || "" : "",
  )

  const [config, setConfig] = useState<DatabaseConfig>({
    host: typeof window !== "undefined" ? localStorage.getItem("db_host") || "localhost" : "localhost",
    user: typeof window !== "undefined" ? localStorage.getItem("db_user") || "root" : "root",
    password: typeof window !== "undefined" ? localStorage.getItem("db_password") || "" : "",
    database:
      typeof window !== "undefined" ? localStorage.getItem("db_database") || "neurobyte_systems" : "neurobyte_systems",
    port: typeof window !== "undefined" ? localStorage.getItem("db_port") || "3306" : "3306",
  })

  const [products, setProducts] = useState<DBProduct[]>([])
  const [loading, setLoading] = useState(false)
  const [connected, setConnected] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [success, setSuccess] = useState<string | null>(null)

  const parseConnectionString = (connStr: string) => {
    try {
      const url = new URL(connStr.replace(/^mysql:\/\//, "mysql://"))
      const newConfig = {
        host: url.hostname || "localhost",
        user: url.username || "root",
        password: url.password || "",
        database: url.pathname.slice(1) || "neurobyte_systems",
        port: url.port || "3306",
      }
      setConfig(newConfig)
      saveConfigToStorage(newConfig)
      setSuccess("Conex√£o parseada com sucesso!")
      setTimeout(() => setSuccess(null), 3000)
    } catch (err) {
      setError("Formato de connection string inv√°lido. Use: mysql://user:password@host:port/database")
      setTimeout(() => setError(null), 5000)
    }
  }

  const saveConfigToStorage = (configToSave: DatabaseConfig) => {
    if (typeof window !== "undefined") {
      localStorage.setItem("db_host", configToSave.host)
      localStorage.setItem("db_user", configToSave.user)
      localStorage.setItem("db_password", configToSave.password)
      localStorage.setItem("db_database", configToSave.database)
      localStorage.setItem("db_port", configToSave.port)
      localStorage.setItem("db_connection_string", connectionString)
    }
  }

  const saveConfig = () => {
    saveConfigToStorage(config)
    setSuccess("Configura√ß√£o salva com sucesso!")
    setTimeout(() => setSuccess(null), 3000)
  }

  const testConnection = async () => {
    setLoading(true)
    setError(null)
    setSuccess(null)

    try {
      const response = await fetch("/api/db/test-connection", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(config),
      })

      const data = await response.json()

      if (response.ok) {
        setConnected(true)
        setSuccess("Conex√£o estabelecida com sucesso!")
        loadProducts()
      } else {
        setConnected(false)
        setError(data.error || "Falha na conex√£o com o banco de dados")
      }
    } catch (err) {
      setConnected(false)
      setError(err instanceof Error ? err.message : "Erro ao testar conex√£o")
    } finally {
      setLoading(false)
    }
  }

  const loadProducts = async () => {
    setLoading(true)
    setError(null)

    try {
      const response = await fetch("/api/db/products")
      const data = await response.json()

      if (response.ok) {
        setProducts(data.products || [])
      } else {
        setError(data.error || "Falha ao carregar produtos")
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : "Erro ao carregar produtos")
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-background to-background/80 p-8">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <div className="flex items-center gap-3 mb-2">
            <div className="w-8 h-8 flex items-center justify-center text-primary text-lg font-bold">üóÑÔ∏è</div>
            <h1 className="text-4xl font-bold font-serif">Conectar MySQL</h1>
          </div>
          <p className="text-foreground/60">
            Configure sua conex√£o com o banco de dados MySQL para sincronizar produtos
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Configura√ß√£o */}
          <div className="lg:col-span-1">
            <Card className="border-gray-200 sticky top-8">
              <CardHeader>
                <CardTitle className="text-lg">Configura√ß√£o do Banco</CardTitle>
                <CardDescription>Insira suas credenciais MySQL</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                {/* Connection String Input */}
                <div>
                  <label className="block text-sm font-medium mb-2">Connection String (MySQL URL)</label>
                  <Input
                    placeholder="mysql://root:@localhost:3306/neurobyte_systems"
                    value={connectionString}
                    onChange={(e) => setConnectionString(e.target.value)}
                  />
                  <Button
                    onClick={() => parseConnectionString(connectionString)}
                    variant="outline"
                    className="w-full mt-2 bg-transparent"
                    disabled={!connectionString}
                  >
                    Parse Connection String
                  </Button>
                </div>

                <div className="border-t border-gray-200 pt-4">
                  <p className="text-xs text-foreground/50 mb-4">Ou configure manualmente:</p>
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Host</label>
                  <Input
                    placeholder="localhost"
                    value={config.host}
                    onChange={(e) => setConfig({ ...config, host: e.target.value })}
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Usu√°rio</label>
                  <Input
                    placeholder="root"
                    value={config.user}
                    onChange={(e) => setConfig({ ...config, user: e.target.value })}
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Senha</label>
                  <Input
                    type="password"
                    placeholder="‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢‚Ä¢"
                    value={config.password}
                    onChange={(e) => setConfig({ ...config, password: e.target.value })}
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Banco de Dados</label>
                  <Input
                    placeholder="neurobyte_systems"
                    value={config.database}
                    onChange={(e) => setConfig({ ...config, database: e.target.value })}
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2">Porta</label>
                  <Input
                    placeholder="3306"
                    value={config.port}
                    onChange={(e) => setConfig({ ...config, port: e.target.value })}
                  />
                </div>

                <div className="pt-4 space-y-2">
                  <Button onClick={saveConfig} variant="outline" className="w-full bg-transparent">
                    Salvar Configura√ß√£o
                  </Button>
                  <Button onClick={testConnection} disabled={loading} className="w-full">
                    {loading ? "Conectando..." : "Testar Conex√£o"}
                  </Button>
                </div>

                {connected && (
                  <Alert className="bg-green-50 border-green-200">
                    <AlertDescription className="text-green-800">‚úì Conectado com sucesso</AlertDescription>
                  </Alert>
                )}
              </CardContent>
            </Card>
          </div>

          {/* Produtos */}
          <div className="lg:col-span-2">
            <Card className="border-gray-200">
              <CardHeader className="flex flex-row items-center justify-between space-y-0">
                <div>
                  <CardTitle>Produtos do Banco de Dados</CardTitle>
                  <CardDescription>
                    {products.length} produto{products.length !== 1 ? "s" : ""} encontrado
                    {products.length !== 1 ? "s" : ""}
                  </CardDescription>
                </div>
                <Button size="sm" variant="outline" onClick={loadProducts} disabled={!connected || loading}>
                  ‚Üª
                </Button>
              </CardHeader>
              <CardContent>
                {error && (
                  <Alert className="mb-4 bg-red-50 border-red-200">
                    <AlertDescription className="text-red-800">‚ö† {error}</AlertDescription>
                  </Alert>
                )}

                {success && (
                  <Alert className="mb-4 bg-green-50 border-green-200">
                    <AlertDescription className="text-green-800">‚úì {success}</AlertDescription>
                  </Alert>
                )}

                {loading ? (
                  <div className="flex items-center justify-center py-12">
                    <div className="text-primary">Carregando...</div>
                  </div>
                ) : products.length === 0 ? (
                  <div className="text-center py-12">
                    <div className="text-4xl mb-4">üóÑÔ∏è</div>
                    <p className="text-foreground/60">Nenhum produto encontrado</p>
                    <p className="text-sm text-foreground/40">Verifique sua conex√£o e tente novamente</p>
                  </div>
                ) : (
                  <div className="space-y-4">
                    {products.map((product) => (
                      <div
                        key={product.id}
                        className="border border-gray-200 rounded-lg p-4 hover:border-primary/50 transition"
                      >
                        <div className="flex items-start justify-between mb-2">
                          <div className="flex-1">
                            <h3 className="font-semibold text-lg">{product.name}</h3>
                            <p className="text-sm text-foreground/60">{product.description}</p>
                          </div>
                          <span className="inline-block px-3 py-1 bg-primary/10 text-primary text-sm rounded-full font-medium">
                            {product.category}
                          </span>
                        </div>

                        <div className="grid grid-cols-3 gap-4 mt-4 pt-4 border-t border-gray-200">
                          <div>
                            <p className="text-xs text-foreground/50">Pre√ßo</p>
                            <p className="text-lg font-semibold">R$ {product.price.toFixed(2)}</p>
                          </div>
                          <div>
                            <p className="text-xs text-foreground/50">Estoque</p>
                            <p className="text-lg font-semibold">{product.stock} unidades</p>
                          </div>
                          <div>
                            <p className="text-xs text-foreground/50">ID</p>
                            <p className="text-lg font-semibold">#{product.id}</p>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </div>

        {/* Instru√ß√µes */}
        <Card className="mt-8 border-gray-200 bg-blue-50/50">
          <CardHeader>
            <CardTitle className="text-lg">Como Usar</CardTitle>
          </CardHeader>
          <CardContent className="space-y-2 text-sm text-foreground/70">
            <p>1. Cole sua connection string MySQL ou configure manualmente os dados</p>
            <p>2. Clique em "Testar Conex√£o" para validar</p>
            <p>3. Seus produtos aparecer√£o abaixo automaticamente</p>
            <p>4. Use o bot√£o de atualizar para sincronizar mudan√ßas</p>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
